@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins&family=Great+Vibes&display=swap');


.hero {
  position: relative;
  height: 75vh;
  padding-top: 4px;
  text-align: center;
  color: #fff;
  overflow: hidden;
}


.hero-logo {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 80px;
  height: 80px;
  background: #fff;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid rgba(255,255,255,.8);
  box-shadow: 0 4px 15px rgba(0,0,0,.3);
  z-index: 5;
}


.hero-nav {
  position: absolute;
  top: 30px;
  right: 20px;
  display: flex;
  gap: 20px;
  z-index: 5;
}

.nav-quote {
  font-family: 'Playfair Display', serif;
  font-size: 0.95rem;
  font-style: italic;
  color: rgba(255, 255, 255, 0.9);
  max-width: 220px;
  line-height: 1.4;
  text-align: right;
  margin-bottom: 6px;
  text-shadow: 1px 1px 6px rgba(0,0,0,0.6);
}


.hero-nav {
  align-items: flex-end;
}
.hero-nav button {
  padding: 10px 22px;
  border-radius: 25px;
  border: none;
  background: linear-gradient(135deg, #ffffff, #ffd700);
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  color: #5a3b0f;
  cursor: pointer;
  transition: all .3s ease;
  box-shadow: 0 6px 18px rgba(255,215,0,.35);
}

.hero-nav button:hover {
  transform: translateY(-2px);
  background: linear-gradient(135deg, #fff8dc, #ffcc33);
  box-shadow: 0 8px 22px rgba(255,215,0,.55);
}


.hero-content {
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 22px;
}


.tagline {
  margin-top: 0;
  font-family: 'Great Vibes', cursive;
  font-size: 0.9rem;
  color: #ffd700;
  text-shadow: 2px 2px 15px rgba(0,0,0,.7);
  text-align: center;
}
.tagline1 {
  margin-top: 0;
  font-family: 'Great Vibes', cursive;
  font-size: 3.8rem;
  color: #d9ce8f;
  text-shadow: 2px 2px 15px rgba(0,0,0,.7);
  text-align: center;
}


.launch-badge {
  padding: 16px 38px;
  border-radius: 30px;
  background: linear-gradient(135deg, #ffffff, #ffd700);
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  color: #5a3b0f;
  letter-spacing: 1px;
  box-shadow: 0 10px 30px rgba(255,215,0,.45);
  transition: transform .3s ease, box-shadow .3s ease;
}

.launch-badge:hover {
  transform: scale(1.05);
  box-shadow: 0 12px 35px rgba(255,215,0,.65);
}


.countdown {
  margin-top: 10px;
}

.countdown h2 {
  color: #ffd700;
  font-family: 'Poppins', sans-serif;
}

.time-boxes {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.time-boxes div {
  padding: 12px 18px;
  background: rgba(0,0,0,.4);
  border-radius: 12px;
  color: #fff;
  font-family: 'Poppins', sans-serif;
}


.bg-slide {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  filter: blur(10px) brightness(.6);
  opacity: 0;
  transition: opacity 1.5s ease, transform 10s ease;
  z-index: -2;
}

.bg-slide.active {
  opacity: 1;
  transform: scale(1.05);
}


.hero-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    rgba(40,20,10,.5),
    rgba(255,255,255,.05)
  );
  z-index: -1;
}


.info-section {
  padding: 90px 15%;
  text-align: center;
  background: #fdf8f1;
  color: #3b2a1a;
}

.info-section.alt {
  background: #f5efe6;
}

.info-section h2 {
  font-family: 'Playfair Display', serif;
  font-size: 3rem;
  color: #9e7c32;
}

.info-section p {
  font-family: 'Poppins', sans-serif;
  font-size: 1.2rem;
  line-height: 1.9;
  max-width: 850px;
  margin: auto;
}


@media (max-width: 768px) {

  .hero-nav {
    flex-direction: column;
    top: 90px;
    gap: 12px;
  }

  .tagline {
    font-size: 2.4rem;
  }

  .launch-badge {
    padding: 14px 28px;
  }

  .info-section {
    padding: 60px 10%;
  }
}
