@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins&family=Great+Vibes&display=swap');

/* Global Styles */
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  color: #2b1b1a;
  background: linear-gradient(#f8f1e7, #efe2c9);
}

/* Sections */
.section {
  padding: 80px 20px;
  text-align: center;
}

/* HERO SECTION */
.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  overflow: hidden;
}

/* Background slideshow */
.bg-slideshow {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  z-index: -2;
  animation: fadeZoom 30s infinite;
}

/* Overlay */
.bg-slideshow::after {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(40, 20, 10, 0.5);
  z-index: -1;
}

/* Slideshow images using keyframes */
@keyframes fadeZoom {
  0%, 100% { opacity: 0; transform: scale(1); }
  10%, 20% { opacity: 1; transform: scale(1.05); }
  30%, 90% { opacity: 0; transform: scale(1); }
}

/* Hero content */
.hero .logo {
  width: 160px;
  margin-bottom: 15px;
}

.hero .tagline {
  font-family: 'Great Vibes', cursive;
  font-size: 3.5rem;
  margin: 15px 0;
}

.hero .subtitle {
  max-width: 500px;
  margin: 20px auto;
  font-size: 1.1rem;
  line-height: 1.8;
}

.hero .launch-badge {
  display: inline-block;
  padding: 12px 30px;
  background: linear-gradient(to right,#c9a24d,#b08a3c);
  color: white;
  border-radius: 30px;
  font-weight: 500;
  margin-top: 25px;
}

/* ANIMATIONS */
.fade-in { animation: fade 1.2s ease; }
.fade-in-delay { animation: fade 1.8s ease; }
.slide-up { animation: slide 1.2s ease; }

@keyframes fade { from {opacity:0;} to {opacity:1;} }
@keyframes slide { from {opacity:0; transform: translateY(40px);} to {opacity:1; transform: translateY(0);} }

/* MOBILE RESPONSIVENESS */
@media (max-width: 768px) {
  .hero .tagline { font-size: 2.5rem; }
  .hero .subtitle { font-size: 1rem; }
}
