
.footer {
  padding: 50px 20px;
  background: linear-gradient(to right, rgba(74,44,42,0.9), rgba(74,44,42,0.8));
  color: white;
  text-align: center;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  box-shadow: 0 -5px 25px rgba(0,0,0,0.2);
}

.footer p {
  font-size: 1.2rem;
  margin-bottom: 20px;
  font-weight: 500;
}

.footer .socials {
  margin-bottom: 20px;
}

.footer .socials a {
  margin: 0 15px;
  text-decoration: none;
  color: white;
  font-weight: 600;
  transition: transform 0.3s ease, color 0.3s ease;
}

.footer .socials a:hover {
  transform: translateY(-2px);
  color: #c9a24d;
}

.footer span {
  display: block;
  margin-top: 10px;
  font-size: 0.9rem;
  opacity: 0.8;
}

@media (max-width: 768px) {
  .footer p { font-size: 1rem; }
  .footer .socials a { margin: 0 10px; font-size: 0.9rem; }
}
