
.signup {
  position: relative;
  padding: 80px 20px;
  text-align: center;
  background: linear-gradient(to right, rgba(250,245,240,0.7), rgba(240,230,210,0.7));
  backdrop-filter: blur(8px); 
  border-radius: 20px;
  max-width: 600px;
  margin: 50px auto;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
}

.signup h2 {
  font-family: 'Playfair Display', serif;
  font-size: 2.2rem;
  margin-bottom: 25px;
  color: #4a2c2a;
  text-shadow: 1px 1px 3px rgba(255,255,255,0.7);
}

.signup form {
  display: flex;
  justify-content: center;
  gap: 12px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.signup input {
  padding: 14px 16px;
  border-radius: 12px;
  border: 1px solid #ccc;
  font-size: 1rem;
  flex: 1 1 220px;
  min-width: 180px;
}

.signup button {
  padding: 14px 24px;
  background: linear-gradient(to right, #c9a24d, #b08a3c);
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.signup button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0,0,0,0.3);
}


@media (max-width: 768px) {
  .signup h2 { font-size: 1.8rem; }
  .signup form { flex-direction: column; gap: 15px; }
  .signup input, .signup button { width: 100%; }
}
